import {useEffect, useState} from 'react';
import {createId} from 'lib/createId';
import {useUpdate} from './useUpdate';
import {useHistory} from 'react-router-dom';
import {checkName} from '../CheckName';



const useTags =() =>{   //封装一个 自定义Hook
  const [tags,setTags] =useState<{id:number ;name:string}[]>([])
  useEffect(()=> {
    let localTags = JSON.parse(window.localStorage.getItem('tags') || '[]')
    if (localTags.length === 0) {
    localTags=[
      {id:createId(),name:'衣服'},
      {id:createId(),name:'饰品'},
      {id:createId(),name:'出行'},
    ]
    }
  setTags(localTags)
  },[])

  useUpdate(()=>{
    window.localStorage.setItem('tags',JSON.stringify(tags))

  },tags)

  const findTag=(id:number) => tags.filter(tag => tag.id === id)[0];
  const findTagindex =(id:number)=>{
    let result =-1;
    for(let i=0; i<tags.length;i++){
      if(tags[i].id ===id){
        result=i;
        break;

      }
    }
    return result;

  }
  const updateTag =(id:number,{name}:{name:string})=>{
    setTags(tags.map(tag =>tag.id === id ?{id,name:name}:tag))

      }

  const history =useHistory();
  const deleteTag =(id:number )=>{
    setTags(tags.filter(tag =>tag.id !==id))
    history.goBack();
    window.alert('删除成功')
  }
    const keepTag =(id:number,{name}:{name:string})=>{
      setTags(tags.map(tag =>tag.id === id ?{id,name:name}:tag))
      history.goBack();
      window.alert('保存成功')

}
  const addTag =() =>{
    const name =window.prompt('标签名为：')
    if(name !== null && name !=='' && checkName(name)){
      setTags([...tags, {id:createId(),name:name}])
    }else{
      window.alert('请输入正确的标签')
    }
  };
  const getName =(id:number) =>{
     const tag = tags.filter( t=>t.id ===id)[0]
   return tag ? tag.name :''


  }
  return {tags,getName,addTag,setTags,findTag,updateTag,findTagindex,deleteTag,keepTag}

}

export {useTags};