import React from 'react';
import {useParams,useHistory} from 'react-router-dom'
import {useTags} from 'hook/useTags';
import Layout from 'components/Layout';
import Icon from 'components/Icon';
import {Button} from 'components/Button';
import styled from 'styled-components';
import {Input} from 'components/input';
import {Center} from 'components/Center';
import {Space} from 'components/Space';


type Params ={
  id:string
}

const ButtonWrapper =styled.div`
  padding:10px;

`;


const InputWrapper =styled.div`
  background:white;
  padding: 0 16px;
  margin-top:8px;

`
const Topbar =styled.header`
      display:flex;
      justify-content:space-between;
     padding: 14px;
     line-height:20px;
     background:white;
      
`


const Tag:React.FC =() =>{

 const {findTag,updateTag,deleteTag,keepTag} =useTags();
 let {id:idString} =useParams<Params>()

  const tag =findTag(parseInt(idString))
  const tagContent =(tag:{id:number,name:string})=>(
    <div>
      <InputWrapper>
        <Input label="标签名" type="text" placeholder="标签名" value={tag.name}
               onChange={(e)=>{
                 updateTag(tag.id,{name:e.target.value})

               }}
        />
      </InputWrapper>
      <Center>
        <Space/>
        <Space/>
        <Space/>
        <ButtonWrapper >
        <Button value={tag.name} onClick={(e) =>{
          keepTag(tag.id, {name: (e.target as HTMLButtonElement).value});
        }}
        >保存</Button>
        </ButtonWrapper>
        <Button onClick={() =>{deleteTag(tag.id);
        }}>删除</Button>

      </Center>
    </div>
  )
  const history =useHistory();
const onClickBack=()=>{
  history.goBack()
}
  return (
    <Layout>
     <Topbar>
     <Icon name="leftt"  onClick={onClickBack}/>
     <span> 编辑标签</span>
       <Icon/>
     </Topbar>
      {tag ?  tagContent(tag) :  <Center> 删除成功!</Center>}

    </Layout>

  )
}


export {Tag};