import styled from 'styled-components';


const Wrapper= styled.section`
  display:flex;
  flex-direction:column;
 > .output{
 background:white;
 line-height:72px;
 font-size:36px;
 text-align:right;
 padding:0 16px;
 box-shadow: inset  0 -5px 5px -5px rgba(0,0,0,0.25),
 inset  0 5px 5px -5px rgba(0,0,0,0.25)
 }
 > .pad{
background:white;
 > button{
 width:25%;
 height:64px;
 float:left;
 font-size:18px;
 border-color:white;
 &.ok{
  height:128px;
  float:right;
  
 }
 &.zero{
 width:50%;
 background:white;
 }
 &:nth-child(1){
 background:white;
 }
 &:nth-child(2),
 &:nth-child(5)
 {
 background:white;
 }
 &:nth-child(3),
 &:nth-child(6),
 &:nth-child(9)
 {
 background:white;
 }
 &:nth-child(4),
 &:nth-child(7),
 &:nth-child(10)
 {
 background:white;
 }
 &:nth-child(8),
 &:nth-child(11),
 &:nth-child(13)
 {
 background:white;
 }
 &:nth-child(12)
 {
 background:white;
 }
  &:nth-child(14)
 {
 background:white;
 }
 }
`;


export {Wrapper};